import sha256 from 'tiny-sha256';

export default class OOAPI {
  constructor(apiKey, secret) {
    this.apiKey = apiKey;
    this.secret = secret;
  }

  getSignature(apiKey, expires, method, path, params={}, body='') {
    params.api_key = apiKey;
    params.expires = expires;
    let hashStr = sha256(this.secret + method.toUpperCase() + path + OOAPI.serialize(params, '') + body);
    let signature = new Buffer(hashStr, 'hex').toString('base64');
    // Remove any trailing = sign
    return signature.replace(/=+$/, '');
  }

  static serialize(params, delimiter) {
    return Object.keys(params).sort((a, b) => {
        if (a[0] < b[0]) {
          return -1;
        } else if (a[0] === b[0]) {
          return 0;
        } else {
          return 1;
        }
      }).map((key) => {
        return key + '=' + params[key];
      }).join(delimiter);
  }

  makeRequest(method, path, params={}, body) {
    let expires = Math.floor(Date.now() / 1000) + 86400;
    let jsonString = body ? JSON.stringify(body) : '';
    let signature = this.getSignature(this.apiKey, expires, method, path, params, jsonString);

    let url = [
      'https://api.ooyala.com' + path,
      [
        OOAPI.serialize(params, '&'),
        ['signature', encodeURIComponent(signature)].join('=')
      ].join('&').replace(/^&+/, '')
    ].join('?');

    if (method === 'get') {
      return fetch(url).then((res) => {
        return res.json();
      });
    } else {
      return fetch(url, {
        method,
        headers: {
          'Accept': 'application/json'
          //'Content-Type': 'application/json'
        },
        body: jsonString
      });
    }
  }
};
