import OOAPI from './OOAPI';

export default class PlayerUI {
  constructor(mb, id) {
    this.mb = mb; // save message bus reference for later use
    this.id = id;
    this.duration = NaN;
    this.playing = false;
    this.init(); // subscribe to relevant events
  }

  init() {
    // subscribe to relevant player events
    this.mb.subscribe(OO.EVENTS.PLAYER_CREATED, 'customUi', this.onPlayerCreate.bind(this));
    this.mb.subscribe(OO.EVENTS.PLAYHEAD_TIME_CHANGED, 'customUi', this.onTimeUpdate.bind(this));
    this.mb.subscribe(OO.EVENTS.CONTENT_TREE_FETCHED, 'customUi', this.onContentReady.bind(this));
    this.api = new OOAPI('Ntd2QyOuEQD51iQ2ZKhjHcKTRqs0.7kTV2', 'pfOqji94y01slK78BRKAMPg-z4z0Puqlc6A2IH-D');
  }

  onPlayerCreate(event, elementId, params) {
    let main = document.querySelector('main');
    let footer = document.querySelector('footer');

    // Add custom UI
    let template = document.querySelector('.myoo-player__ui-template');
    let playerUiElement = document.importNode(template.content, true);

    this.playButton = playerUiElement.querySelector('.play-button');
    this.playButton.addEventListener('click', this.onPlay.bind(this), false);
    this.currentTimeElement = playerUiElement.querySelector('.current-time');
    this.durationElement = playerUiElement.querySelector('.duration');
    this.slider = playerUiElement.querySelector('.myoo-player__slider');
    this.slider.addEventListener('input', this.onSlide.bind(this), false);
    this.slider.addEventListener('change', this.onSlideStop.bind(this), false);

    this.initTitle(playerUiElement);

    main.insertBefore(playerUiElement, footer);
  }

  initTitle(playerUiElement) {
    let path = location.pathname;

    this.embeddedId = path.slice(path.lastIndexOf('/') + 1);
    this.titleInput = playerUiElement.querySelector('#title-edit');
    this.titleLabel = playerUiElement.querySelector('#title-label');

    this.api.makeRequest('get', '/v2/assets/' + this.embeddedId)
    .then(
      (info) => {
        this.titleLabel.innerHTML = info.name;
        componentHandler.upgradeDom();

        this.titleInput.addEventListener('keydown', (e) => {
          if (e.keyCode === 13) { // Enter key
            let newTitle = this.titleInput.value;
            this.api.makeRequest('PATCH', '/v2/assets/' + this.embeddedId, void 0, {name: newTitle})
            .then(
              (v) => {
                console.log(v);
                this.titleLabel.innerHTML = newTitle;
                this.titleInput.value = '';
                this.titleInput.dispatchEvent(new Event('input'));
              },
              (err) => {
                console.log(err);
              }
            );
          }
        }, false); 
      },
      (err) => {
        console.error(err);
      }
    );
  }

  onTimeUpdate(event, time=0, duration=0, buffer) {
    let val = Math.round(time);
    this.currentTimeElement.textContent = val;
    this.durationElement.textContent = Math.round(duration);
    this.slider.value = val;
  }

  onContentReady(event, content) {
    let duration = Math.round(content.time);

    this.durationElement.textContent = duration;
    this.playButton.disabled = false;
    this.slider.min = 0;
    this.slider.max = duration;
    this.slider.step = 1;
    this.slider.disabled = false;
  }

  onPlay() {
    if (this.playButton.textContent === 'Play') {
      this.play();
    } else {
      this.pause();
    }
  }

  play() {
    this.mb.publish(OO.EVENTS.PLAY);
    this.playing = true;
    this.playButton.textContent = 'Pause';
  }

  pause() {
    this.mb.publish(OO.EVENTS.PAUSE);
    this.playing = false;
    this.playButton.textContent = 'Play';
  }

  seek(seconds) {
    this.mb.publish(OO.EVENTS.SEEK, seconds);
  }

  onSlide() {
    if (this.playing) {
      this.pause();
    }
  }

  onSlideStop() {
    this.seek(this.slider.value);
    if (!this.playing) {
      this.play();
    }
  }

  static get __end_marker() {
    return true;
  }
};
